import React, { Component } from "react";
import { connect } from "react-redux";
import { Field, reduxForm } from "redux-form";
import { hideModal } from "../../../actions/datamaster_action";
import { HiidenFiled, ReanderField } from "../../../components/helpers/field";
import ValidasiMasterDataHadiah from "../../../Validasi/ValidasiMasterDataHadiah.jsx";
import Loading from 'react-fullscreen-loading';

const maptostate = (state) => {
  if (state.datamaster.ShowModalDataHadiah !== null) {
    return {
      initialValues: {
        nama_hadiah: state.datamaster.ShowModalDataHadiah.nama_hadiah,
        poin: state.datamaster.ShowModalDataHadiah.poin,
        stock: state.datamaster.ShowModalDataHadiah.stock,
        kode_hadiah: state.datamaster.ShowModalDataHadiah.kode_hadiah,
      },
    };
  }
};
class FormDataHadiah extends Component {
  state = {
    aktif: false,
  };
  
  componentDidMount() {
    setTimeout(() => {
      this.props.isEdit
        ? document.getElementById("nama_hadiah").focus()
        : document.getElementById("nama_hadiah").focus();
    }, 100);
  }
  render() {
    return (
      <form onSubmit={this.props.handleSubmit} autoComplete="off">
        <Field
          id="kode_hadiah"
          tabIndex="1"
          name="kode_hadiah"
          component={HiidenFiled}
          type="hidden"
        />
        <Field
          id="nama_hadiah"
          tabIndex="2"
          name="nama_hadiah"
          component={ReanderField}
          type="text"
          label=" Nama Hadiah"
          placeholder="Masukan  Nama Hadiah"
        />
        <Field
          tabIndex="3"
          name="poin"
          component={ReanderField}
          type="number"
          label="Poin"
          placeholder="Masukan Poin"
        />
        <Field
          tabIndex="4"
          name="stock"
          component={ReanderField}
          type="number"
          label=" Stock"
          placeholder="Masukan Stock"
          onBlur={() => this.setState({ aktif: true })}
        />
        <div className="row">
          <div className="col-12 text-right">
            <button
              className="btn btn-white"
              onClick={() => this.props.dispatch(hideModal())}
              type="button"
              disabled={this.props.isLoading}
            >
              Batal
            </button>
            &nbsp;
            <button
              tabIndex="5"
              className={this.state.aktif ? "btn btn-primary" : "btn btn-white"}
              disabled={this.props.isLoading}
              type="submit"
            >
              {this.props.isLoading ? (
                <>
                  <i className="fas fa-spinner fa-spin"></i> &nbsp; Sedang
                  Menyimpan
                </>
              ) : (
                "Simpan Data"
              )}
            </button>
          </div>
        </div>
        {this.props.isLoading ? (
        <Loading 
        loading
        background="rgba(0, 0, 0, 0.35)"
         loaderColor="rgba(94, 147, 117, 1)" />
      ) : null}
      </form>
    );
  }
}

FormDataHadiah = reduxForm({
  form: "ModalHadiah",
  enableReinitialize: true,
  validate: ValidasiMasterDataHadiah,
})(FormDataHadiah);
export default connect(maptostate, null)(FormDataHadiah);
