import React from "react";
import { Link } from "react-router-dom";
import { Panel, PanelHeader } from "../../../components/panel/panel.jsx";
import BootstrapTable from "react-bootstrap-table-next";
import ToolkitProvider, {
  Search,
  CSVExport,
} from "react-bootstrap-table2-toolkit";
import paginationFactory from "react-bootstrap-table2-paginator";
import {
  ShowModalDataHadiah,
  showModal,
  getDataHadiah,
  hideModal,
} from "../../../actions/datamaster_action.jsx";
import { connect } from "react-redux";
import Swal from "sweetalert2";
import {
  NotifSucces,
  ToastNotification,
} from "../../../components/helpers/notification.jsx";
import { deleteData, postData, putData } from "../../../config/axios.jsx";
import { reset } from "redux-form";
import { ErrorHandling } from "../../../components/helpers/function.jsx";
import FormDataHadiah from "./FormDataHadiah.jsx";
import ModalGlobal from "../../../components/helpers/ModalGlobal.jsx";

const { SearchBar } = Search;
const { ExportCSVButton } = CSVExport;

const maptostate = (state) => {
  return {
    datahadiah: state.datamaster.getDataHadiah,
    hideModal: state.datamaster.modalDialog,
  };
};
const hapusDataHadiah = (nama, kode, dispatch) => {
  Swal.fire({
    // title: "Anda Yakin !!",
    // text: "Ingin Menghapus Data Ini ?",
    html:
      "Apakah Anda Yakin Ingin " +
      "Menghapus " +
      "<h1><b>Nama Hadiah " +
      nama +
      "</b> ?</h1>",
    icon: "warning",
    position: "top-center",
    cancelButtonText: "Tidak",
    showCancelButton: true,
    confirmButtonText: "OK",
    showConfirmButton: true,
  }).then((result) => {
    if (result.isConfirmed) {
      deleteData("hadiah/delete/by-kode-hadiah/", {
        kode_hadiah: kode,
      }).then(() => {
        NotifSucces("Data Berhasil Di Hapus").then(() =>
          dispatch(getDataHadiah())
        );
      }).catch((err) => {
        ToastNotification("error", err.response.data)
      })
    }
    // if (result.isConfirmed) {
    //   console.log(kode);
    //   let data = {
    //     kode_hadiah: kode,
    //   }
    //   deleteData("hadiah/delete/by-kode-hadiah",data).then(() => {
    //     NotifSucces("Data Berhasil Di Hapus").then(() =>
    //       dispatch(getDataHadiah())
    //     );
    //   });
    //   // deleteData('hadiah/delete/by-kode-hadiah', kode_hadiah).then((res) => {
    //   //   NotifSucces("Data Berhasil Di Hapus");
    //   //   this.props.dispatch(getDataHadiah())
    //   // }).catch((err) => {
    //   //   ToastNotification("info",'Data Gagal Di hapus')
    //   // })
    // }
  });
};
class MasterHadiah extends React.Component {
  constructor(props) {
    super(props);
    this.state = {
      isEdit: false,
      isLoading: false,
      modalDialog: false,
      columns: [
        {
          dataField: "nama_hadiah",
          text: "Nama Hadiah",
          sort: true,
        },
        {
          dataField: "poin",
          text: "Poin",
        },
        {
          dataField: "stock",
          text: "Stock",
        },
        {
          dataField: "action",
          text: "Action",
          csvExport: false,
          headerClasses: "text-center",
          formatter: (rowcontent, row) => {
            let dataEdit = {
              kode_hadiah: row.kode_hadiah,
              nama_hadiah: row.nama_hadiah,
              stock: row.stock,
              poin: row.poin,
            };
            return (
              <div className="row text-center">
                <div className="col-12">
                  <button
                    onClick={() => this.formEditDataHadiah(dataEdit)}
                    className="btn btn-primary mr-3"
                  >
                    <i className="fa fa-edit"></i>
                  </button>
                  <button
                    onClick={() =>
                      hapusDataHadiah(
                        row.nama_hadiah,
                        row.kode_hadiah,
                        this.props.dispatch
                      )
                    }
                    className="btn btn-danger"
                  >
                    <i className="fa fa-trash"></i>
                  </button>
                </div>
              </div>
            );
          },
        },
      ],
    };
    this.handleSubmit = this.handleSubmit.bind(this);
  }

  formEditDataHadiah(dataEdit) {
    this.props.dispatch(showModal());
    this.props.dispatch(ShowModalDataHadiah(dataEdit));
    this.setState({
      isEdit: true,
    });
  }
  ShowModalDataHadiah() {
    this.props.dispatch(showModal());
    this.props.dispatch(ShowModalDataHadiah(false));
    this.setState({
      isEdit: false,
    });
  }

  componentDidMount() {
    this.props.dispatch(getDataHadiah());
  }
  handleSubmit(data) {
    this.setState({
      isLoading: true,
    });

    let data_edit = {
      kode_hadiah: data.kode_hadiah,
      nama_hadiah: data.nama_hadiah,
      poin: data.poin,
      stock: data.stock,
    };
    let data_post = {
      nama_hadiah: data.nama_hadiah,
      poin: data.poin,
      stock: data.stock,
    };
    this.state.isEdit
      ? putData("hadiah/edit/by-kode-hadiah", data_edit)
          .then(() => {
            this.props.dispatch(hideModal());
          })
          .then(() => {
            ToastNotification("success", "Data Berhasil Diedit");
          })
          .then(() => {
            this.props.dispatch(getDataHadiah());
          })
          .then(() => {
            this.setState({ isLoading: false });
          })
          .then(() => {
            this.props.dispatch(reset("ModalHadiah"));
          })
        .catch((error) => {
          ToastNotification("info", "Terjadi Kesalahan Saat Mengirim Data")
          this.setState({ isLoading: false });
          })
      : postData("hadiah/add", data_post)
          .then(() => {
            this.props.dispatch(hideModal());
          })
          .then(() => {
            ToastNotification("success", "Data Berhasil Simpan");
          })
          .then(() => {
            this.props.dispatch(getDataHadiah());
          })
          .then(() => {
            this.props.dispatch(reset("ModalHadiah"));
          })
          .then(() => {
            this.setState({ isLoading: false });
          })
          .catch((error) =>
            ErrorHandling(
              error,
              "sales/reactive/by-kode-sales/",
              data.kode_sales,
              data_post,
              this.props.dispatch,
              getDataHadiah(),
              hideModal(),
              "ModalHadiah"
            ).then(() => {
              this.setState({ isLoading: false });
            })
          );
  }
  render() {
    return (
      <div>
        <ol className="breadcrumb float-xl-right">
          <li className="breadcrumb-item">
            <Link to="#">Data Master</Link>
          </li>
          <li className="breadcrumb-item active">Master Data Hadiah</li>
        </ol>
        <h1 className="page-header">Master Data Hadiah </h1>
        <Panel>
          <PanelHeader>Master Data Hadiah</PanelHeader>
          <br />

          {/* Master Data Hadiah */}
          <div className="container">
            <ToolkitProvider
              keyField="kondisi"
              data={this.props.datahadiah || []}
              columns={this.state.columns}
              search
              exportCSV={{
                fileName: "Export Master Data Hadiah.csv",
              }}
            >
              {(props) => (
                <div className="row">
                  <div className="col-6">
                    <button
                      onClick={() => this.ShowModalDataHadiah()}
                      className="btn btn-primary"
                    >
                      {" "}
                      <i className="fa fa-plus"></i>{" "}
                    </button>
                  </div>
                  <div className="col-6">
                    <div className="text-right">
                      <SearchBar {...props.searchProps} />
                    </div>
                  </div>
                  <hr />
                  <div className="col-12">
                    <BootstrapTable
                      pagination={paginationFactory()}
                      {...props.baseProps}
                    />
                    <br />
                    <ExportCSVButton {...props.csvProps}>
                      Export CSV!!
                    </ExportCSVButton>
                  </div>
                </div>
              )}
            </ToolkitProvider>
          </div>
          <br />
          {/* End Master Pinjaman */}
          <ModalGlobal
            title={
              this.state.isEdit
                ? "Edit Data Data Hadiah"
                : "Tambah Data Data Hadiah"
            }
            size="P"
            content={
              <FormDataHadiah
                isLoading={this.state.isLoading}
                isEdit={this.state.isEdit}
                onSubmit={(data) => this.handleSubmit(data)}
              />
            }
          />
          {/* End Tambah Master Data Hadiah  */}
        </Panel>
      </div>
    );
  }
}

export default connect(maptostate, null)(MasterHadiah);
